<template>
    <div class="account-spotwallet">
        <Header/>
        <MenuAccount/>
        <div class="warp-body">
            <div class="account-header">
                <h1>Spot Wallet</h1>
            </div>
            <div class="account-body">
                <div class="w-500">
                    <div class="f-15">Equity Value</div>
                    <div class="mt-20 f-18">0.00000000 BTC <font class="ml-10"></font><font color="gray" class="ml-10">0.00000000 USDT</font></div>
                </div>
                <div class="mt-10">
                    <div class="table-min-header">
                        <div class="head mobile-width-30" style="width: 20%;">Name currency</div>
                        <div class="head mobile-none" style="width: 20%;text-align: left;">Total</div>
                        <div class="head mobile-width-30" style="width: 20%;text-align: left;">Available</div>
                        <div class="head mobile-none" style="width: 20%;text-align: left;">In Order</div>
                        <div class="head mobile-width-40" style="width: 30%;text-align: right;">Action</div>
                    </div>
                    <div style="overflow-x: hidden;overflow-y: scroll;height: 65dvh;">
                    <div class="table-min-body roboto" v-for="asset in assets" :key="asset.id">
                        <div class="body mobile-width-30" style="width: 20%;">
                            <img :src="asset.img" style="margin-top:0px;float: left;margin-right: 14px;" height="26">
                            <div style="float: left;margin-top: -5px;">
                                <div class="f-14 w-500">{{ asset.symbol }}</div>
                                <div class="f-12">{{ asset.asset_name }}</div>
                            </div>
                        </div>
                        <div class="body f-13 w-400 pt-15 mobile-none" style="width: 20%;text-align: left;">{{  asset.balances.length > 0 ? asset.balances[0].total : '0' }}</div>
                        <div class="body f-13 w-400 pt-15 mobile-width-30" style="width: 20%;text-align: left;">{{  asset.balances.length > 0 ? asset.balances[0].available : '0' }}</div>
                        <div class="body f-13 w-400 pt-15 mobile-none" style="width: 20%;text-align: left;">{{  asset.balances.length > 0 ? asset.balances[0].in_order : '0' }}</div>
                        <div class="body f-13 w-400 pt-18 mobile-width-40" style="width: 30%;text-align: right;">
                            <button v-if="asset.depositDisabled == false" class="btn-deposit roboto" @click="deposit(asset.symbol)" data="Deposit"></button>
                            <a v-if="asset.withdrawDisabled == false" class="btn-action roboto w-400" :href="'/account/main/withdraw?symbol='+asset.symbol">Withdraw</a>
                            <a v-if="asset.Ticker.length  > 0" class="btn-action roboto w-400 mobile-none" :href="'/trade/'+asset.Ticker[0].ticker_id">Trade</a>
                        </div>
                    </div>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
import { API_URL } from "../../config.js";
export default {
    name : 'Spotwallet',
    data() {
        return {
            spotWallets : [],
            assets : []
        }
    },
    created () {
        this.getSpotWallet();
    },
    methods : {
        getSpotWallet() {
            let vm = this;
            axios.get(API_URL+'/my/spot-wallet').then((res) => {
                vm.spotWallets = res.data.user;
                vm.assets = res.data.assets;
            })
        },
        deposit(symbol) {
            let url_link = '/account/main/deposit?symbol='+symbol;
            this.$router.push(url_link)
        }
    }
}
</script>
<style scoped>
.account-spotwallet {
    margin: 0;
    padding: 0;
    height: auto;
    width: auto;
    background-color: #ffffff !important;
}
</style>



