</template>
<script>
import axios from 'axios';
import { API_URL } from "../config.js";
import Footer from '@/components/layout/Footer.vue'
export default {
    name: "Fee",
    components : {
        Footer
    },
    metaInfo() {
        return { 
            title: "Trading Fees | ZiiPex.com",
            meta: [
                { name: 'description', content:  'ZiiPex - The most trusted cryptocurrency trading platform which provides real-time trading services including Bitcoin (BTC), Ethereum (ETH) and Tether (USDT) trading. customers places a trade on ZiiPex!'},
                { property: 'og:title', content: "Trading Services"},
                { property: 'og:url', content: "https://ziipex.com/fees"},
                { property: 'og:site_name', content: 'ZiiPex'},
                { property: 'og:type', content: 'website'},
                { name: 'robots', content: 'index,follow'}
            ]
        }
    },
    data() {
        return {
            assets : [],
            isSelected : 0,
            tabs : [
                { title : 'VIP Holding', id : 'vip_holding', display : true },
                { title : 'Spot', id : 'spot',display : true}
            ],
        };
    },
    created () {
        let vm = this;
        axios.get(API_URL + '/assets').then(function (response) {
            vm.assets = response.data.assets;
            // console.log(response.data.assets)
        })
    },
    methods : {
        tabTrade(tabname) {
            let x = this.tabs;
            for (let i = 0; i < x.length; i++) {
                if(x[i].id != tabname)
                {
                    document.getElementById(x[i].id).style.display = "none"; 
                }else{
                    document.getElementById(tabname).style.display = "block";
                    this.isSelected = i;
                }
            }
        },
    }
};
</script>
<style scoped>
.box-fee-top  {
    padding-top: 30px;
    background: #000;
    height: 170px;
    /* border :1px solid #ddd; */
}

.box-fee-body {
    padding-bottom: 50px;
}

.top-bar-fee {
    margin: 10px 0px;
}


.top-bar-fee a {
    background-color: transparent;
    margin-left: 0px;
    padding: 10px 30px;
    color: #a9b0c3;
    text-align: center;
    vertical-align: middle;
    text-decoration: none;
    font-size: 13px;
    font-weight: 600;
    border-radius: 5px;
    transition: all 0.4s ease;
}

.top-bar-fee a:hover {
    background-color: #0d111d;
}

.top-bar-fee a.active {
    color: #fff;
    background-color: #358bed2e;
}
</style>


