<template>
    <div class="account-spotwallet">
        <Header/>
        <MenuAccount/>
        <div class="warp-body">
            <div class="account-header">
                <h1>Funding Wallet</h1>
            </div>
            <div class="account-body">
                <div class="w-500">
                    <div class="f-15">Estimated Balance</div>
                    <div class="mt-20 f-18">0.00000000 BTC <font class="ml-10"></font><font color="gray" class="ml-10">$0.00000000</font></div>
                </div>
                <div class="mt-10">
                    <div class="table-min-header">
                        <div class="head mobile-width-40" style="width: 20%;">Name currency</div>
                        <div class="head mobile_hide" style="width: 20%;text-align: left;">Total</div>
                        <div class="head mobile-width-30" style="width: 20%;text-align: left;">Available</div>
                        <div class="head mobile_hide" style="width: 15%;text-align: left;">In Order</div>
                        <div class="head mobile_hide" style="width: 15%;text-align: left;">Value</div>
                        <div class="head mobile-width-30" style="width: 10%;">Action</div>
                    </div>
                    <div style="overflow-x: hidden;overflow-y: scroll;height: 65dvh;">
                    <div class="table-min-body" v-for="asset in assets" :key="asset.id">
                        <div class="body mobile-width-40" style="width: 20%;">
                            <img :src="asset.img" style="margin-top:0px;float: left;margin-right: 14px;" height="26">
                            <div style="float: left;margin-top: -5px;">
                                <div class="f-15 w-500">{{ asset.symbol }}</div>
                                <div class="f-12">{{ asset.asset_name }}</div>
                            </div>
                        </div>
                        <div class="body mobile_hide" style="width: 20%;text-align: left;">{{  asset.balances.length > 0 ? asset.balances[0].total : '0.00000' }}</div>
                        <div class="body mobile-width-30" style="width: 20%;text-align: left;">{{  asset.balances.length > 0 ? asset.balances[0].available : '0.00000' }}</div>
                        <div class="body mobile_hide" style="width: 15%;text-align: left;">{{  asset.balances.length > 0 ? asset.balances[0].in_order : '0.00000' }}</div>
                        <div class="body mobile_hide" style="width: 15%;text-align: left;">{{  asset.balances.length > 0 ? asset.balances[0].value : '0.00000' }}</div>
                        <div class="body mobile-width-30" style="width: 10%;">
                            <a style="color: #ff8c00;margin-right:10px;">Transfer</a>
                        </div>
                    </div>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
import { API_URL } from "../../config.js";
export default {
    name : 'Fundingwallet',
    data() {
        return {
            spotWallets : [],
            assets : []
        }
    },
    created () {
        this.getSpotWallet();
    },
    methods : {
        getSpotWallet() {
            let vm = this;
            axios.get(API_URL+'/my/funding-wallet').then((res) => {
                vm.spotWallets = res.data.user;
                vm.assets = res.data.assets;
            })
        }
    }
}
</script>
<style scoped>
.account-spotwallet {
    margin: 0;
    padding: 0;
    height: auto;
    width: auto;
    background-color: #ffffff !important;
}
</style>



