<template>
      <div class="deposit">
            <Header/>
            <div class="container">
                  <div class="row">
                        <div class="box-deposit">
                              <div class="w-600 f-20 border-bottom pb-10">Deposit</div>
                              <div class="mt-20 w-500">Currency</div>
                              <div class="box-symbol-deposit">
                                    <div class="deposit-asset-first">
                                          <img :src="asset.img" height="25" alt="">
                                    </div>
                                    <div class="deposit-asset-center">
                                          <div class="w-500">{{ asset.symbol }}</div>
                                          <div class="f-12">{{ asset.asset_name }}</div>
                                    </div>
                                    <div class="deposit-asset-last">
                                          Change <i class="fa-solid fa-angles-right"></i>
                                    </div>
                              </div>
                              <div class="box-symbol-form">
                                    <div class="mt-20 w-500">Method</div>
                                    <nav class="nav-deposit-one">
                                          <a class="tab-deposit w-400" :class="{ 'active' : (index == isSelected ? true : '') }" v-for="tab,index in tabs" :key="tab.index" @click="tabTrade(tab.id)" v-show="tab.display == true">
                                                <div class="w-500 text-black">{{ tab.title }}</div>
                                                <div class="mt-5 tx-gray2 f-13" v-html="tab.sub_title"></div>
                                          </a>
                                    </nav>
                                    <tabs>
                                          <tab class="tab-deposit-box" id="tab_address" style="display:block;">
                                                <div class="w-500">Network <span class="tx-red f-12 w-400 ml-10">Make sure you select the right network</span></div>
                                                <nav class="nav-deposit-one">
                                                      <a class="tab-deposit w-400" :class="{ 'active' : (index == isSelected_network ? true : '') }" v-for="tab2,index in network_tabs" :key="tab2.index" @click="tabNetwork(tab2.id)">
                                                            <div class="w-500 f-13 text-black">{{ tab2.network.network_name }}</div>
                                                            <div class="tx-gray2 f-12" v-html="'Fee: 0 '+asset.symbol"></div>
                                                      </a>
                                                </nav>
                                                <tabs>
                                                      <tab class="tab-deposit-box" id="tab_erc20" style="display:block;">
                                                            <div class="deposit-box-qrcode">
                                                                  <div class="" style="margin-left: auto;margin-right: auto;">
                                                                        <div class="name-tab-top">{{ asset.symbol }} Address</div>
                                                                  </div>
                                                                  <div class="qr-code" style="text-align: center;width: 100%;padding-top: 20px;">
                                                                        <div style="background-color: #fff;border-radius: 5px;border : 1px solid #fff;width: 152px;padding :10px;margin-left: auto;margin-right: auto;">
                                                                              <qr-code :size="130" :text="address" error-level="L"></qr-code>
                                                                        </div>
                                                                        
                                                                        
                                                                  </div>
                                                                  <div class="text-primary w-500 f-14 mt-20">{{ address }}</div>
                                                                  <div class="w-500 f-14 mt-10" v-if="tag != null">Destination Tag : <span class="text-primary">{{  tag  }}</span></div>
                                                                  <!-- <button class="copy-text" @click="copy_text()">Copy </button> -->
                                                                  <button class="btn-deposit mt-10 roboto text-grey w-100" @click="copy_text()" data=" Copy"><i class="fa-solid fa-clone"></i></button>
                                                            </div>
                                                            <div class="deposit-box-detail mt-20">
                                                                  Deposit details
                                                                  <div class="mt-10">
                                                                         
                                                                  </div>
                                                            </div>
                                                      </tab>
                                                      <tab class="tab-deposit-box" id="tab_trc20" style="display:none;">trc20</tab>
                                                      <tab class="tab-deposit-box" id="tab_omni" style="display:none;">tab_omni</tab>
                                                      <tab class="tab-deposit-box" id="tab_eos" style="display:none;">tab_eos</tab>
                                                      <tab class="tab-deposit-box" id="tab_polygon" style="display:none;">tab_polygon</tab>
                                                </tabs>
                                          </tab>
                                          <tab class="tab-deposit-box-visa" id="tab_visa_mastercard" style="display:none;">
                                                blockchain
                                          </tab>
                                          
                                    </tabs>
                              </div>
                        </div>
                  </div>
            </div>
      </div>
</template>
<script>
import { API_URL } from "../../../config.js";
import VueQRCodeComponent from "vue-qrcode-component";

export default {
      name: "MainDeposit",
      components: {
            "qr-code": VueQRCodeComponent
      },
      data() {
            return {
                  symbol : 'BTC',
                  asset : {},
                  isSelected : 0,
                  isSelected_network : 0,
                  tabs : [
                        { title : 'Address', sub_title : 'Blockchain',id : 'tab_address', display : true },
                        { title : 'Buy Crypto',sub_title : 'Visa / MasterCard <i class="fa-brands fa-cc-visa f-15"></i> <i class="fa-brands fa-cc-mastercard f-15"></i>', id : 'tab_visa_mastercard', display : false}
                  ],
                  network_tabs : [
                        // { title : 'ERC20', sub_title : 'Fee: 0',id : 'tab_erc20', display : true },
                        // { title : 'TRC20',sub_title : 'Fee: 0', id : 'tab_trc20', display : true},
                        // { title : 'OMNI',sub_title : 'Fee: 0', id : 'tab_omni', display : true},
                        // { title : 'EOS',sub_title : 'Fee: 0', id : 'tab_eos', display : true},
                        // { title : 'POLYGON',sub_title : 'Fee: 0', id : 'tab_polygon', display : true},
                  ],
                  address : '--------------------',
                  transactions : []
            }
      },
      created() {

            let symbol = this.$route.query.symbol;
            if (typeof symbol != "undefined") {
                  this.checkAddress(symbol);
            }else{

            }
      },
      methods : {
            async copy_text() {
                  var Url = this.address;
                  await navigator.clipboard.writeText(Url);
                  this.$toast.success("Copied Address", {
                    timeout: 1200
                });
            },
            checkAddress (symbol) {
                  let vm = this;
                  axios.get(API_URL + "/my/deposit?symbol=" + symbol).catch(function(error) {
                        if (
                              error.response.status == 401 ||
                              error.response.data.message === "Unauthenticated."
                        ) {
                              localStorage.setItem("username", "");
                              vm.$auth.destroyToken();
                              vm.gust = false;
                              vm.$router.go("/");
                        }
                  }).then(response => {
                        // console.log(response.data);
                        if(response.data.status == true) {
                              vm.asset = response.data.asset;
                              vm.network_tabs = response.data.asset.AssetNetwork;
                              vm.address = response.data.address;
                              vm.tag = response.data.tag;
                        }else{
                              window.location.href = '/account/main'
                        }
                        // { title : 'ERC20', sub_title : 'Fee: 0',id : 'tab_erc20', display : true },
                  })
            },
            tabTrade(tabname) {
                  let x = this.tabs;
                        for (let i = 0; i < x.length; i++) {
                        if(x[i].id != tabname)
                        {
                              document.getElementById(x[i].id).style.display = "none"; 
                        }else{
                              document.getElementById(tabname).style.display = "block";
                              this.isSelected = i;
                        }
                  }
            },
            tabNetwork (tabname) {
                  let x = this.network_tabs;
                        for (let i = 0; i < x.length; i++) {
                        if(x[i].id != tabname)
                        {
                              document.getElementById(x[i].id).style.display = "none"; 
                        }else{
                              document.getElementById(tabname).style.display = "block";
                              this.isSelected_network = i;
                        }
                  }
            }
      }
}
</script>
<style scoped>
.deposit {
      background: #f0f2f4;
}

</style>


