<template>
  <div id="app">
    <!-- <Header/> -->
    <router-view/>
    <!-- <Footer/> -->
  </div>
</template>

<script>
export default {
  name: 'App',
  metaInfo() {
      return { 
          title: "Trading exchange | Sandnex.com",
          htmlAttrs: {
            lang: 'en',
            amp: true
          },
          meta: [
              { name: 'description', content:  '.'},
              { property: 'og:title', content: ""},
              { property: 'og:site_name', content: 'ZiiPEX'},
              {property: 'og:type', content: 'website'},
              {name: 'robots', content: 'index,follow'}
          ]
      }
  },
  created () {
    localStorage.setItem("lang", 'en');
    this.$cookies.set("theme", 'dark', "1y")
  }
}
</script>

<style>
@import url('./assets/css/style.css');
@import url('./assets/css/font-awesome.css');
</style>



