                <div class="mt-30 w-500">My Assets</div>
                <!-- <button @click="toast">Toast it!</button> -->
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name : 'Exchange_Openorder',
    data() {
        return {

        }
    },methods : {
        toast  () {
            this.$toast.success("My toast content", {
                timeout: 1500
            });
        }
    }
}
</script>
<style scoped>
.account-overview {
    margin: 0;
    padding: 0;
    height: auto;
    width: auto;
    background-color: #ffffff !important;
}
</style>



