<template>
    <div class="register poppin">
        <div class="base-container__left">
            <div style="top:2.3em;margin-bottom: 2.3em;">
                <div class="base-container__left-content">
                    <div class="base-logo">
                        <router-link to="/"><img src="../../assets/img/logo.png" width="140" style="margin-bottom: 6rem;"
                                alt=""></router-link>
                    </div>
                    <div class="base-container__left-title">
                        <h1>
                            <span class="first-wrap">The Most</span><router-link to="/login" class="typewrite"
                                data-period="1500" data-type='[ "Trusted", "Secure", "Reliable", "Assets" ]'>
                                <span class="wrap">Secure</span>
                            </router-link>
                            <br>Cryptocurrency
                            <br>Exchange.
                        </h1>
                        <p class="poppin">Get started with the easiest and most secure platform to buy, sell, trade, and
                            earn cryptocurrencies.</p>
                    </div>
                </div>

            </div>
        </div>
        <div class="base-container__right">
            <div class="base-container__right-content">
                <div class="form-components__title">
                    <h2>Register</h2>
                </div>
                <div class="form-components__body">
                    <form @submit.prevent="register">
                        <div class="form-components__input-text">
                            <div class="form-components__block">
                                <label for="UsernameOrEmail" class="form-components__block-label">Email:</label>
                                <div class="form-components__block-item block-input-text">
                                    <span class="form-components__block-icon">
                                        <img src="../../assets/img/email-icon.png" class="mt_2" height="20" alt="">
                                    </span>
                                    <input autocomplete="new-email" type="email" class="form-components__block-input"
                                        v-model="from.email" required autofocus>
                                </div>
                            </div>
                            <div class="form-components__block">
                                <label for="password" class="form-components__block-label w-500">Password:</label>
                                <div class="form-components__block-item block-input-text">
                                    <span class="form-components__block-icon">
                                        <img src="../../assets/img/padlock-icon.png" class="mt_2" height="20" alt="">
                                    </span>
                                    <input autocomplete="new-password" type="password" class="form-components__block-input"
                                        pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}" v-model="from.password" maxlength="32"
                                        required>
                                </div>
                            </div>
                            <div class="form-components__block">
                                <label for="password" class="form-components__block-label w-500">Confirm Password:</label>
                                <div class="form-components__block-item block-input-text">
                                    <span class="form-components__block-icon">
                                        <img src="../../assets/img/padlock-icon.png" class="mt_2" height="20" alt="">
                                    </span>
                                    <input autocomplete="new-password" type="password" class="form-components__block-input"
                                        v-model="from.password_confirmation" required>
                                    <small class="tx-red" v-show="not_match">Password not match</small>
                                </div>
                                <div class="password-validate">
                                    <p id="letter" class="invalid">a lowercase letter</p>
                                    <p id="capital" class="invalid">a capital (uppercase) letter</p>
                                    <p id="number" class="invalid">a number</p>
                                    <p id="length" class="invalid">8-32 characters</p>
                                    <p id="match" class="invalid">confrim password</p>
                                </div>
                            </div>
                            <div class="form-components__block">
                                <label for="password" class="form-components__block-label w-500">Referral Code:</label>
                                <div class="form-components__block-item block-input-text">
                                    <span class="form-components__block-icon">
                                        <img src="../../assets/img/padlock-icon.png" class="mt_2" height="20" alt="">
                                    </span>
                                    <input autocomplete="new-ref" type="text" class="form-components__block-input"
                                        v-model="from.ref" placeholder="optional">
                                </div>
                            </div>
                            <div class="form-components__block">
                                <label class="checker">I agree to the
                                    <a href="/terms-and-conditions" target="_blank" class="text-yellow">Terms of Use</a>
                                    and <a href="/privacy-policy" target="_blank" class="text-yellow">Privacy
                                        Policy</a><input type="checkbox" v-model="from.agree" required="required" /><span
                                        class="checkmark"></span></label>
                                <small v-show="check_agree" class="font-12" style="color: red;display: none;">Please check
                                    this box if you
                                    want proceed</small>
                            </div>


                            <div class="form-components__block">
                                <div id="captcha" style="min-height: 39px; background-color: #181c2b"></div>
                                <div v-show="show_verify" class="font-12" style="color: red; display: none">
                                    * Click to verify
                                </div>
                                <div class="form-components__block-item block-button">
                                    <button type="submit" class="form-components__block-button">
                                        Register
                                    </button>
                                </div>
                            </div>
                            <div class="form-components__block flex">
                                <router-link :to="'/login'" class="form-components__block-link">Already
                                    registered?</router-link>
                            </div>

                        </div>
                    </form>

                </div>


            </div>

        </div>
    </div>
</template>

<script>
import axios from "axios";
import { API_URL } from "../../config.js";
export default {
    name: "Register",
    data() {
        return {
            host: new URL(location.href).host,
            loginAuth: true,
            error: false,
            success: false,
            show_verify: false,
            validate: false,
            check_validate: true,
            not_match: false,
            check_email: false,
            check_email_validate: false,
            check_agree: false,
            from: {
                email: "",
                password: "",
                password_confirmation: "",
                ref: "",
                agree: false,
                data: []
            }
        };
    },
    metaInfo() {
        return {
            title: "Register | ZiiPEX.com",
            htmlAttrs: {
                lang: "en",
                amp: true
            },
            meta: [
                { name: "description", content: "Get started with the easiest and most secure platform to buy, sell, trade, and earn cryptocurrencies." },
                { property: "og:title", content: "Register | ZiiPEX.com" },
                { property: "og:site_name", content: "ZiiPex" },
                { property: "og:type", content: "website" },
                { name: "robots", content: "index,follow" }
            ]
        };
    },
    mounted() { },
    watch: {
        "from.password"(passInput) {
            var letter = document.getElementById("letter");
            var capital = document.getElementById("capital");
            var number = document.getElementById("number");
            var length = document.getElementById("length");

            var lowerCaseLetters = /[a-z]/g;
            var upperCaseLetters = /[A-Z]/g;
            var numbers = /[0-9]/g;
            if (passInput.match(lowerCaseLetters)) {
                letter.classList.remove("invalid");
                letter.classList.add("valid");
            } else {
                letter.classList.remove("valid");
                letter.classList.add("invalid");
            }

            if (passInput.match(upperCaseLetters)) {
                capital.classList.remove("invalid");
                capital.classList.add("valid");
            } else {
                capital.classList.remove("valid");
                capital.classList.add("invalid");
            }

            if (passInput.match(numbers)) {
                number.classList.remove("invalid");
                number.classList.add("valid");
            } else {
                number.classList.remove("valid");
                number.classList.add("invalid");
            }

            if (passInput.length >= 8 && passInput.length <= 32) {
                length.classList.remove("invalid");
                length.classList.add("valid");
            } else {
                length.classList.remove("valid");
                length.classList.add("invalid");
            }
        },
        "from.password_confirmation"(passInput) {
            var match = document.getElementById("match");

            if (passInput != '' && passInput == this.from.password) {
                match.classList.remove("invalid");
                match.classList.add("valid");
            } else {
                match.classList.remove("valid");
                match.classList.add("invalid");
            }
        }
    },
    created() {
        let vm = this;
        if (typeof this.$route.query.ref !== "undefined") {

            this.from.ref = this.$route.query.ref;
        }
        initGeetest4(
            {
                captchaId: "3bc40107fd4cd66d70ff242ea56da494",
                // product: 'popup',
                btnHeight: "39px",
                btnWidth: "100%",
                language: "en"
            },
            function (captcha) {
                captcha.appendTo("#captcha");

                captcha.onReady(function () {
                    vm.validate = false;
                })
                    .onSuccess(function (res) {
                        vm.validate = true;
                        vm.show_verify = false;
                        vm.from.data = captcha.getValidate();
                    })
                    .onError(function () {
                        vm.validate = false;
                    });
            }
        );
    },
    methods: {
        showPassword() {
            this.$swal.fire(
                'Success!',
                'Login successs yet!',
                'success'
            )
            var input_pass = document.getElementById("password");
            var input_eye = document.getElementById("eye");
            if (input_pass.type === "password") {
                input_pass.type = "text";
                input_eye.classList.remove("fa-eye");
                input_eye.classList.add("fa-eye-slash");
            } else {
                input_pass.type = "password";
                input_eye.classList.remove("fa-eye-slash");
                input_eye.classList.add("fa-eye");
            }
        },
        register() {
            let vm = this;
            vm.success = false;
            vm.error = false;
            console.log("regis");
            if (vm.from.agree == true) {

                if (vm.validate == true) {
                    axios.post(API_URL + "/register", vm.from).then((res) => {

                        if (res.data.status) {
                            // vm.success = true;
                            // let token = res.data.token;
                            // vm.$store.state.user = res.data.email;
                            // localStorage.setItem("username", res.data.email);
                            // vm.$auth.setToken(token);
                            vm.$router.go("/");
                        } else {

                            if (res.data.error == "email_used" || res.data.error == "user_used") {

                                vm.$swal.fire({
                                    icon: 'warning',
                                    title: res.data.message,
                                    text: 'Please enter a valid email address',
                                })

                                vm.check_email = true;
                            } else {
                                vm.check_email = false;
                            }

                            if (res.data.error == "email_validate") {
                                vm.check_email_validate = true;
                                vm.$swal.fire({
                                    icon: 'warning',
                                    title: res.data.message,
                                    text: 'Please enter a valid email address',
                                })
                            } else {
                                vm.check_email_validate = false;
                            }

                            if (res.data.error == "confirm_pass") {
                                vm.$swal.fire({
                                    icon: 'warning',
                                    title: res.data.message,
                                    text: 'Passwords do not match',
                                })
                                vm.not_match = true;
                            } else {
                                vm.not_match = false;
                            }

                            vm.error = true;
                        }

                    }).catch(function (error) {
                        vm.error = true;
                    });
                } else {
                    // vm.show_verify = true;
                    this.$toast.warning('Click to verify', {
						timeout: 1500
				    });
                }
            } else {
                if (this.from.agree == false) {
                    vm.check_agree = true;
                } else {
                    vm.check_agree = false;
                }
            }
        }
    }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.register {
    align-items: stretch;
    display: flex;
    justify-content: space-between;

}

.first-wrap {
    padding-right: 15px;
}

.typewrite {
    color: #ffc107;
}
</style>


