// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import VueAxios from 'vue-axios'
import Vuex from 'vuex'
import Auth from './auth.js'
import VueMoment from 'vue-moment'
import moment from 'moment-timezone'
import VueCookies from 'vue-cookies'

import VueMeta from 'vue-meta'
import VueSweetalert2 from 'vue-sweetalert2';
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
/* import specific icons */
import { faUserSecret } from '@fortawesome/free-solid-svg-icons'


import Toast from "vue-toastification";
import "vue-toastification/dist/index.css";

const toast_options = {
  // You can set your default options here
  transition: "Vue-Toastification__bounce",
  maxToasts: 5,
  newestOnTop: true,
  enter: "fade-enter-active",
  leave: "Vue-Toastification__bounce-leave-active",
  transition: "fade-move"
};


/* add icons to the library */
library.add(faUserSecret)
Vue.component('font-awesome-icon', FontAwesomeIcon)
import { store } from './vuex/store.js'

Vue.config.devtools = true
import { API_URL } from './config.js'
import Notifications from 'vue-notification'

import '../src/assets/js/gt4.js'
import 'sweetalert2/dist/sweetalert2.min.css';

Vue.config.productionTip = false

Vue.use(VueAxios, axios)
Vue.use(Auth)
Vue.use(Vuex)
Vue.use(Notifications)
Vue.use(VueCookies,{ expires: '1d'})
Vue.use(VueSweetalert2);
Vue.use(VueMoment, {
  moment,
})

Vue.use(Toast, toast_options);
Vue.use(VueMeta)
// Vue.use(VueMeta, {
//   refreshOnceOnNavigation: true
// })


import Header from '@/components/layout/Header.vue'
import Footer from '@/components/layout/Footer.vue'
import MenuAccount from '@/components/layout/MenuAccount.vue'

Vue.component('Header', Header)
Vue.component('Footer', Footer)
Vue.component('MenuAccount', MenuAccount)

window.axios = require('axios');
axios.defaults.baseURL = API_URL;

window.axios.defaults.headers.common = {
  'Content-Type': 'application/json',
  'Accept': 'application/json',
  'Access-Control-Allow-Credentials': true,
  'Access-Control-Allow-Origin': true,
  'Authorization': 'Bearer ' + localStorage.token
};

// const API_URL_MAIN = process.env.API_URL || API_URL
if (Vue.auth.isAuthenticated()) {
  axios.defaults.headers.common['Authorization'] = 'Bearer ' + Vue.auth.getToken()
  axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
  axios.defaults.headers.post['maxContentLength'] = 100000000
  axios.defaults.headers.post['maxBodyLength'] = 100000000
  // axios.defaults.headers.common['Content-length'] = 200;
  // axios.defaults.headers.common['Connection'] = 'Keep-Alive';
}

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.forVisitor)) {
    if (Vue.auth.isAuthenticated()) {
      next({
        path: '/'
      })
    } else {
      next()
    }
  } else if (to.matched.some(record => record.meta.forAuth)) {
    if (!Vue.auth.isAuthenticated()) {
      next({
        path: '/login'
      })
    } else {
      next()
    }
  } else {
    next()
  }
}
)

/* eslint-disable no-new */
var app = new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



