<template>
    <div class="market">
        <Header/>
        <div class="box-announcement">
            New token listing [ AOMZ ] by ziipex.com : pre sale start 04/07/23
        </div>
        <div class="container">
            <div class="row">
                <h2 class="pt-3">Markets</h2>
                <div class="top-markets">
                    <div class="box-top-market">
                        <div class="head">Top Gainer</div>
                        <div id="loading-topgainer" class="loading-top-market">
                            <div class="wave"></div>
                            <div class="wave"></div>
                            <div class="wave"></div>
                            <div class="wave"></div>
                            <div class="wave"></div>
                            <div class="wave"></div>
                            <div class="wave"></div>
                            <div class="wave"></div>
                            <div class="wave"></div>
                            <div class="wave"></div>
                        </div>

                        <ul class="display-none" id="load-topgainer-done">
                            <li v-for="tg in gainers" :key="tg.sort" @click="goUrl(tg.ticker_id)">
                                <span><div style="display: flex;justify-content: left;"><img :src="tg.asset.img" height="20" style="margin-top: -1px;margin-right:7px;" alt=""> {{ tg.target}}</div></span>
                                <span class="text-right">{{ formatNumber2Dig(tg.last_price) }}</span>
                                <span v-if="tg.percent != 0" class="text-right" :class="tg.percent > 0 ? 'tx-green' : 'tx-red'">{{ tg.percent > 0 ? '+' : '' }}{{ formatNumber2Dig(tg.percent) }}%</span>
                                <span v-else class="tx-gray text-right">{{ formatNumber2Dig(tg.percent) }}%</span>
                            </li>
                            
                        </ul>
                    </div>
                    <div class="box-top-market">
                        <div class="head">Highlight</div>
                        <ul>
                            <li v-for="tg in highlight" :key="tg.sort" @click="goUrl(tg.ticker_id)">
                                <span><div style="display: flex;justify-content: left;"><img :src="tg.asset.img" height="20" style="margin-top: -1px;margin-right:7px;" alt=""> {{ tg.target}}</div></span>
                                <span class="text-right">{{ formatNumber2Dig(tg.last_price) }}</span>
                                <span v-if="tg.percent != 0" class="text-right" :class="tg.percent > 0 ? 'tx-green' : 'tx-red'">{{ tg.percent > 0 ? '+' : '' }}{{ formatNumber2Dig(tg.percent) }}%</span>
                                <span v-else class="tx-gray text-right">{{ formatNumber2Dig(tg.percent) }}%</span>
                            </li>
                        </ul>
                    </div>
                    <div class="box-top-market">
                        <div class="head">New Listing</div>
                        <ul>
                            <li v-for="tg in new_listing" :key="tg.sort" @click="goUrl(tg.ticker_id)">
                                <span><div style="display: flex;justify-content: left;"><img :src="tg.asset.img" height="20" style="margin-top: -1px;margin-right:7px;" alt=""> {{ tg.target}}</div></span>
                                <span class="text-right">{{ formatNumber2Dig(tg.last_price) }}</span>
                                <span v-if="tg.percent != 0" class="text-right" :class="tg.percent > 0 ? 'tx-green' : 'tx-red'">{{ tg.percent > 0 ? '+' : '' }}{{ formatNumber2Dig(tg.percent) }}%</span>
                                <span v-else class="tx-gray text-right">{{ formatNumber2Dig(tg.percent) }}%</span>
                            </li>
                            
                        </ul>
                    </div>
                    <div class="box-top-market">
                        <div class="head">Highest Volume</div>
                        <ul>
                            <li v-for="tg in highlight" :key="tg.sort" @click="goUrl(tg.ticker_id)">
                                <span><div style="display: flex;justify-content: left;"><img :src="tg.asset.img" height="20" style="margin-top: -1px;margin-right:7px;" alt=""> {{ tg.target}}</div></span>
                                <span class="text-right">{{ formatNumber2Dig(tg.last_price) }}</span>
                                <span v-if="tg.percent != 0" class="text-right" :class="tg.percent > 0 ? 'tx-green' : 'tx-red'">{{ tg.percent > 0 ? '+' : '' }}{{ formatNumber2Dig(tg.percent) }}%</span>
                                <span v-else class="tx-gray text-right">{{ formatNumber2Dig(tg.percent) }}%</span>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <div class="box-class">

            <div class="container">
                <div class="top-bar-market">
                    <a href="#"><i class="fa fa-star"></i> Favorites</a>
                    <a href="/market" class="active">All Cryptos</a>
                    <a href="#">Spot Markets</a>
                    <a href="#">New Listing</a>
                </div>
                <div class="table-class-header text-white">
                    <div class="head" style="width: 300px;">Name</div>
                    <div class="head" style="width: 200px;text-align: right;">Price</div>
                    <div class="head" style="width: 200px;text-align: right;">Change</div>
                    <div class="head" style="width: 200px;text-align: right;">24 Volume</div>
                    <div class="head" style="width: 200px;text-align: right;">24 Market</div>
                    <div class="head" style="width: 100px;text-align: right;">Action</div>
                </div>
                <div id="loading-crypto" class="loading-market-all">
                    <div class="wave"></div>
                    <div class="wave"></div>
                    <div class="wave"></div>
                    <div class="wave"></div>
                    <div class="wave"></div>
                    <div class="wave"></div>
                    <div class="wave"></div>
                    <div class="wave"></div>
                    <div class="wave"></div>
                    <div class="wave"></div>
                </div>
                <div class="display-none" id="load-crypto-done">
                    <div class="table-class-body text-white" v-for="market in markets" :key="market._id">
                        <div class="body" style="width: 300px;display: flex;justify-content: start;">
                            <img :src="market.asset.img" height="25" style="margin-right:10px;" alt="">
                            <font size="4" style="font-weight: 500;">{{ market.target }}</font> <small style="color: #b1afaf;margin-left: 7px;">{{ market.asset.asset_name }}</small>
                        </div>
                        <div class="body" style="width: 200px;text-align: right;">{{ formatNumber2Dig(market.last_price) }}</div>
                        <div class="body tx-bold" v-if="market.percent != 0" style="width: 200px;text-align: right;" :class="market.percent > 0 ? 'tx-green' : 'tx-red'">{{ parseFloat(market.percent).toFixed(2) }}%</div>
                        <div class="body tx-bold" v-else style="width: 200px;text-align: right;">{{ parseFloat(market.percent).toFixed(2) }}%</div>
                        <div class="body" style="width: 200px;text-align: right;">{{ formatNumber6Dig(market.target_volume) }}</div>
                        <div class="body" style="width: 200px;text-align: right;">{{ formatCmpctNumber2Dig(market.base_volume) }}</div>
                        <div class="body" style="width: 100px;text-align: right;"><a :href="'/trade/'+market.ticker_id" style="color: #ff8c00;">Trade</a></div>
                    </div>
                </div>
                

            </div>
            <div class="container">
                <div class="row">
                    <div class="pagination">
                        <!-- <a href="#">&laquo;</a> -->
                        <a :class="page.active ? 'active' : ''" :href="page.page == 0 || page.page == pagingCounter ? '#' : page.href" @click="change_page(page.page,'next')" v-for="page in paginations" :key="page.page"><span v-html="page.html"></span></a>
                        
                        <!-- <a href="#">&raquo;</a> -->
                    </div>
                </div>
            </div>
        </div>
        <Footer/>
    </div>
</template>
<script>
import axios from 'axios';
import { API_URL } from "../config.js";

export default {
    name: "Market",
    metaInfo() {
        return {
            title: "Cryptocurrency Market | ZiiPex.com",
            htmlAttrs: {
                lang: 'en',
                amp: true
            },
            meta: [
                { name: 'description', content: '.' },
                { property: 'og:title', content: "http://ziipex.com/market" },
                { property: 'og:site_name', content: 'ZiiPex' },
                { property: 'og:type', content: 'website' },
                { name: 'robots', content: 'index,follow' }
            ]
        }
    },
    data() {
        return {
            gainers : [],
            highlight : [],
            new_listing : [],
            paginations : [
                // { page : 1,active : true},
                // { page : 2,active : false},
                // { page : 3,active : false},
                // { page : 4,active : false},
            ],
            pages : 0,
            pagingCounter : 0,
            totalPages : 0,
            markets : []
        }
    },
    created () {


        this.topGainer();
        this.topHighlight();
        this.newListing();

        let params_page = this.$route.query.page
        if(typeof params_page != 'undefined')
        {
            var page = parseInt(params_page);
        }else {
            var page =  '0';
        }

        let vm = this;
        axios.get(API_URL+'/tickers?page='+page).then((res) => {
            // console.log(res.data);
            if(res.data.status)
            {

                document.getElementById("loading-crypto").style.visibility = "hidden";
                document.getElementById("loading-crypto").style.display = 'none';
                document.getElementById("load-crypto-done").style.visibility = "visible";
            
                let totalDocs = res.data.ticker_length;
                let limit = res.data.limit;
                let totalPages = res.data.count_page;
                vm.totalPages = totalPages;
                // let pagingCounter = 1;
                
                let active_page = page != '0' ? parseInt(page) : 1;

                var data_pages = vm.paginations;
                var pagingCounter = totalPages+1;
                vm.pagingCounter = pagingCounter;

                for (let index = 0; index <= pagingCounter; index++) {
                    if(index == 0)
                    {
                        data_pages.push({ page :index,html : '&laquo',active : false,href : '/market?page=0'})
                    }else if(index+1 == parseInt(pagingCounter)+1) {
                        data_pages.push({ page :index,html : '&raquo',active : false,href : '/market?page='+pagingCounter})
                    }else{
                       data_pages.push({ page :index,html : index,active :  index == active_page ? true : false,href :'/market?page='+index}) 
                    }
                    
                }

                vm.markets = res.data.data;

                // console.log(res.data.ticker_length)
            }
        })

    },
    methods : {
        formatNumber (number) {
            let options = {
                compactDisplay: "short",
                minimumFractionDigits: 2,
            };
            const usformatter = Intl.NumberFormat("en-US", options);
            return usformatter.format(number);
        },
        formatNumber2Dig (number) {
            let options = {
                compactDisplay: "short",
                minimumFractionDigits: 2,
            };
            const usformatter = Intl.NumberFormat("en-US", options);
            return usformatter.format(number);
        },
        formatNumber6Dig (number) {
            let options = {
                notation: "compact",
                compactDisplay: "short",
                minimumFractionDigits: 6,
            };
            const usformatter = Intl.NumberFormat("en-US", options);
            return usformatter.format(number);
        },
        formatCmpctNumber2Dig(number) {
            let options = {
                notation: "compact",
                compactDisplay: "short",
                minimumFractionDigits: 0,
            };
            const usformatter = Intl.NumberFormat("en-US", options);
            return usformatter.format(number);
        },
        formatCmpctNumber(number) {
            let options = {
                notation: "compact",
                compactDisplay: "short",
                minimumFractionDigits: 0,
            };
            const usformatter = Intl.NumberFormat("en-US", options);
            return usformatter.format(number);
        },
        numberWithCommas(x) {
            x = x.toString();
            var pattern = /(-?\d+)(\d{3})/;
            while (pattern.test(x))
                x = x.replace(pattern, "$1,$2");
            return x;
        },
        change_page(data,page_change) {
            let params_page = this.$route.query.page
            if(typeof params_page != 'undefined')
            {
                var page = parseInt(params_page);
            }else {
                var page =  '0';
            }

            if(page_change == 'next' && data != this.pagingCounter && page > 1)
            {
                let prev_page = page-1;
                window.location = '/market?page='+prev_page;
            }

            if(page_change == 'next' && data == this.pagingCounter && page >= 1 && page < this.totalPages) {
                let next_page = page+1;
                window.location = '/market?page='+next_page;
            }
            
            
        },
        topGainer () {
            let vm = this;
            axios.get(API_URL+'/top-gainer').then(function (response) {
                console.log(response.data);
                if(response.data.status) {

                    vm.gainers = response.data.top_gainer;

                    document.getElementById("loading-topgainer").style.visibility = "hidden";
                    document.getElementById("loading-topgainer").style.display = 'none';
                    document.getElementById("load-topgainer-done").style.visibility = "visible";
                }
                
            })
        },
        topHighlight () {
            let vm = this;
            axios.get(API_URL+'/top-highlight').then(function (response) {

                vm.highlight = response.data.top_gainer;
            })
        },
        newListing () {
            let vm = this;
            axios.get(API_URL+'/new-listing').then(function (response) {
                vm.new_listing = response.data.new_listing;
            })
        },
        goUrl (url) {
            window.location = '/trade/'+url;
        }
    }
}
</script>


