<template>
    <div class="sidebar">
        <a :class="{ active : path == '/account/overview' ? true : false }" href="/account/overview">Overview</a>
        <a :class="{ active : path == '/account/main' ? true : false }" href="/account/main">Spot Wallet</a>
        <a :class="{ active : path == '/account/funding' ? true : false }" href="/account/funding">Funding Wallet</a>
        <a :class="{ active : path == '/account/earn' ? true : false }" href="/account/earn">Lend & Earn</a>
    </div>
</template>
<script>
export default {
    name: "MenuAccount",
    data() {
        return  {
            path : '/account/overview'
        }
    },
    created () {

        if(this.$router.currentRoute.path != '')
        {
            this.path = this.$router.currentRoute.path;
        }
    }
}
</script>
<style scoped>

.sidebar {
    position: absolute;
    float: left;
    padding-top :48px;
    background: #fff;
    border-right: 1px solid #ddd;
    width: 220px;
    height: 100vh;
    overflow: auto;
    z-index: 999;
}

.sidebar a {
  display: block;
  color: black;
  padding: 13px;
  text-decoration: none;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 0.3px;
  border-left : 4px solid #fff;
}
 
.sidebar a.active {
  background-color: #a1a1a125;
  border-left : 4px solid #2e3bc8;
  color: #383838;
}

.sidebar a:hover:not(.active) {
  background-color: #a1a1a125;
  border-left : 4px solid #a1a1a125;
  color: #383838;
}

@media screen and (max-width: 700px) {
  .sidebar {
    width: 100%;
    height: auto;
    position: relative;
  }
  .sidebar a {float: left;}
  div.content {margin-left: 0;}
}

@media screen and (max-width: 400px) {
  .sidebar a {
    text-align: center;
    float: none;
  }
}
</style>


