<template>
      <div class="Listing">
            <Header/>
            <div class="container">
                  <div class="row">
                        <div class="box-top-apply">
                              Appication Listing
                        </div>
                        <div class="apply-listing">
                              <div class="box-apply">
                                    <div class="apply-header">If you want your coin to be listed on Sandnex, please apply by providing your coin/token information details below:</div>
                                    <div class="apply-body mt-20">
                                          <div class="form-group">
                                                <label for="Email" class="w-500">Email</label>
                                                <input type="email" class="mt-10" v-model="from.email" id="Email" required autofocus autocomplete="off">
                                          </div>
                                          <div class="form-group mt-20">
                                                <label for="asset_type" class="w-500">Asset Type</label>
                                                <select class="mt-10" id="asset_type">
                                                      <option value="token">Token</option>
                                                      <option value="coin">Coin</option>
                                                </select>
                                          </div>
                                          <div class="form-group mt-20">
                                                <label for="project" class="w-500">Project Name</label>
                                                <input type="text" class="mt-10" v-model="from.project_name" id="project" required>
                                          </div>
                                          <div class="form-group mt-20">
                                                <label for="token" class="w-500">Token Name [e.g. Bitcoin,Ethereum,..]</label>
                                                <input type="text" class="mt-10" v-model="from.token_name" id="token" required>
                                          </div>
                                          <div class="form-group mt-20">
                                                <label for="ticker" class="w-500">Ticker Name [e.g. BTC,ETH,..]</label>
                                                <input type="text" class="mt-10" v-model="from.ticker_name" id="ticker" required>
                                          </div>
                                          <div class="form-group mt-20">
                                                <label for="website" class="w-500">Website</label>
                                                <input type="text" class="mt-10" v-model="from.website" id="website" required placeholder="https://">
                                          </div>
                                          <div class="form-group mt-20">
                                                <label for="nature" class="w-500">BlockChain [e.g. https://bscscan.com/token/...]</label>
                                                <input type="text" class="mt-10" v-model="from.blockchain" id="nature" required>
                                          </div>
                                          <div class="form-group mt-20">
                                                <label for="github_link" class="w-500">Github Project</label>
                                                <input type="text" class="mt-10" v-model="from.github_link" id="github_link" required>
                                          </div>
                                          <div class="form-group mt-20">
                                                <button class="mt-20 btn btn-primary w-100" @click="submit_apply()">Submit Apply Listing</button>
                                          </div>
                                          
                                    </div>
                              </div>
                              
                        </div>
                  </div>
            </div>
            <Footer/>
      </div>
</template>

<script>

import axios from 'axios';
import { API_URL } from "../config.js";
import Header from '@/components/layout/Header.vue'
import Footer from '@/components/layout/Footer.vue'

export default {
      name: 'Listing',
      components : {
            Header,
            Footer
      },
      metaInfo() {
            return {
                  title: "Refferal | ZiiPex.com",
                  meta: [
                        { name: 'description', content: 'Invite friends register and trade on ZiiPex Earn commissions and rewards whenever one of your referred customers places a trade on Sandnex!' },
                        { property: 'og:title', content: "" },
                        { property: 'og:url', content: "https://ziipex.com/apply-listing" },
                        { property: 'og:site_name', content: 'Ziipex' },
                        { property: 'og:type', content: 'website' },
                        { name: 'robots', content: 'index,follow' }
                  ]
            }
      },
      data() {
            return {
                  from : {
                        type : 'token',
                        email : '',
                        project_name : '',
                        token_name : '',
                        ticker_name : '',
                        website : '',
                        blockchain : '',
                        github_link : '',
                  }
            }
      },
      methods : {
            submit_apply () {
                  let vm = this;
                  console.log('application')
                  axios.post(API_URL+'/apply-listing',this.from).then(function (res) {
                        if(res.data.status) {
                              vm.$swal('Successfully!','You Submit information','success');
                              vm.from.email = ''
                              vm.from.project_name = ''
                              vm.from.token_name = ''
                              vm.from.ticker_name = ''
                              vm.from.website = ''
                              vm.from.blockchain = ''
                              vm.from.github_link = ''
                        }
                        
                  })
            }
      }
}
</script>
<style scoped>
/* @import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@100;200;300;400;500&display=swap'); */
/* .Listing {
      font-family: 'IBM Plex Sans', sans-serif !important;
} */
</style>


