<template>
	<div class="login poppin">
		<div class="base-container__left">
			<div style="top:2.3em;margin-bottom: 2.3em;">
				<div class="base-container__left-content">
					<div class="base-logo">
						<router-link to="/"><img src="../../assets/img/logo.png" width="140" style="margin-bottom: 6rem;"
								alt=""></router-link>
					</div>
					<div class="base-container__left-title">
						<h1>
							<span class="first-wrap">The Most</span><router-link to="/login" class="typewrite" data-period="1500" data-type='[ "Trusted", "Secure", "Reliable", "Assets" ]'>
								<span class="wrap">Secure</span>
							</router-link>
							<br>Cryptocurrency
							<br>Exchange.
						</h1>
						<p class="poppin">Get started with the easiest and most secure platform to buy, sell, trade, and earn cryptocurrencies.</p>
					</div>
				</div>

			</div>
		</div>
		<div class="base-container__right">
			<div class="base-container__right-content">
				<div class="form-components__title">
					<h2>Login</h2>
				</div>
				<div class="form-components__body">
					<form @submit.prevent="login">
						<div class="form-components__input-text">
							<div class="form-components__block">
								<label for="UsernameOrEmail" class="form-components__block-label">Email :</label>
								<div class="form-components__block-item block-input-text">
									<span class="form-components__block-icon">
										<img src="../../assets/img/email-icon.png" class="mt_2" height="20" alt="">
									</span>
									<input autocomplete="new-email" type="email" class="form-components__block-input" v-model="from.email" required autofocus>
								</div>
							</div>
							<div class="form-components__block">
								<label for="password" class="form-components__block-label w-500">Password:</label>
								<div class="form-components__block-item block-input-text">
									<span class="form-components__block-icon">
										<img src="../../assets/img/padlock-icon.png" class="mt_2" height="20" alt="">
									</span>
									<input autocomplete="new-password" type="password" class="form-components__block-input" v-model="from.password" required>
								</div>
								<!-- <span class="span-pass" @click="showPasswordLogin"><i class="fa fa-eye" id="eye"
									aria-hidden="true"></i></span> -->
							</div>

							<div class="form-components__block">
								<div id="captcha" style="min-height: 39px; background-color: #181c2b"></div>
								<div v-show="show_verify" class="font-12" style="color: red; display: none">
									* Click to verify
								</div>

								<div class="form-components__block-item block-button">
									<button type="submit" class="form-components__block-button">
										Login
									</button>
								</div>
							</div>
							<div class="form-components__block flex">
									<router-link :to="'/register'" class="form-components__block-link form-components__block-link-first text-left">Create new account</router-link>
									<router-link :to="'/forgot-password'" class="form-components__block-link">Forgot your password?</router-link>
								</div>
							
						</div>
					</form>
					
				</div>


			</div>

		</div>
	</div>
</template>

<script>
import axios from "axios";
import { API_URL } from "../../config.js";

export default {
	name: "Login",
	data() {
		return {
			host: new URL(location.href).host,
			loginAuth: true,
			error: false,
			success: false,
			submitting: false,
			show_verify: false,
			validate: false,
			from: {
				email: "",
				password: "",
				data: []
			}
		};
	},
	metaInfo() {
		return {
			title: "Login | ZiiPEX.com",
			htmlAttrs: {
				lang: "en",
				amp: true
			},
			meta: [
				{ name: "description", content: "Get started with the easiest and most secure platform to buy, sell, trade, and earn cryptocurrencies." },
				{ property: "og:title", content: "Login | ZiiPEX.com" },
				{ property: "og:site_name", content: "ZiiPEX" },
				{ property: "og:type", content: "website" },
				{ name: "robots", content: "index,follow" }
			]
		};
	},
	created() {
		let vm = this;
		var TxtType = function(el, toRotate, period) {
        this.toRotate = toRotate;
        this.el = el;
        this.loopNum = 0;
        this.period = parseInt(period, 10) || 1500;
        this.txt = '';
        this.tick();
        this.isDeleting = false;
    };

    TxtType.prototype.tick = function() {
        var i = this.loopNum % this.toRotate.length;
        var fullTxt = this.toRotate[i];

        if (this.isDeleting) {
        this.txt = fullTxt.substring(0, this.txt.length - 1);
        } else {
        this.txt = fullTxt.substring(0, this.txt.length + 1);
        }

        this.el.innerHTML = '<span class="wrap">'+this.txt+'</span>';

        var that = this;
        var delta = 200 - Math.random() * 100;

        if (this.isDeleting) { delta /= 2; }

        if (!this.isDeleting && this.txt === fullTxt) {
        delta = this.period;
        this.isDeleting = true;
        } else if (this.isDeleting && this.txt === '') {
        this.isDeleting = false;
        this.loopNum++;
        delta = 500;
        }

        setTimeout(function() {
        that.tick();
        }, delta);
    };

    window.onload = function() {
        var elements = document.getElementsByClassName('typewrite');
        for (var i=0; i<elements.length; i++) {
            var toRotate = elements[i].getAttribute('data-type');
            var period = elements[i].getAttribute('data-period');
            if (toRotate) {
              new TxtType(elements[i], JSON.parse(toRotate), period);
            }
        }
        // INJECT CSS
        var css = document.createElement("style");
        css.type = "text/css";
        css.innerHTML = ".typewrite > .wrap { border-right: 0.08em solid #fff;}";
        document.body.appendChild(css);
    };
		// console.log(this.$store.state.user);

		initGeetest4(
			{
				captchaId: "3bc40107fd4cd66d70ff242ea56da494",
				// product: 'popup',
				btnHeight: "39px",
				btnWidth: "100%",
				language: "en"
			},
			function (captcha) {
				captcha.appendTo("#captcha");

				captcha
					.onReady(function () {
						vm.validate = false;
					})
					.onSuccess(function (res) {
						vm.validate = true;
						vm.show_verify = false;
						vm.from.data = captcha.getValidate();
					})
					.onError(function () {
						vm.validate = false;
					});
			}
		);
	},
	mounted() {
	},
	methods: {
		showPasswordLogin() {
			var input_pass = document.getElementById("password");
			var input_eye = document.getElementById("eye");
			if (input_pass.type === "password") {
				input_pass.type = "text";
				input_eye.classList.remove("fa-eye");
				input_eye.classList.add("fa-eye-slash");
			} else {
				input_pass.type = "password";
				input_eye.classList.remove("fa-eye-slash");
				input_eye.classList.add("fa-eye");
			}
		},
		loginWithGoogle() {
			this.$gAuth
				.signIn()
				.then(GoogleUser => {
					// on success do something
					console.log("GoogleUser", GoogleUser);
					var userInfo = {
						loginType: "google",
						google: GoogleUser
					};
					this.$store.commit("setLoginUser", userInfo);
					vm.$router.push("/account/overview");
				})
				.catch(error => {
					console.log("error", error);
				});
		},
		login() {
			let vm = this;
			vm.success = false;
			vm.error = false;
			vm.submitting = true;
			if (vm.validate == true) {
				vm.axios.post(API_URL + "/login", this.from)
					.catch(function (error) {
						if (
							typeof error.response === "undefined" ||
							error.response.status == 401 ||
							error.response.data.message === "Unauthenticated."
						) {
							this.$swal.fire({
								icon: "warning",
								title: "Important message",
								text: "Username or passoword fail."
							});

							vm.error = true;
							vm.submitting = false;
						}
					})
					.then(response => {
						if (response.status == 200 || response.status == 201) {

							if (response.data.status == true) {
								this.$toast.success('Login successfully', {
										timeout: 1500
								});
								vm.success = true;
								let token = response.data.token;
								let expiration = response.data.expires_in;
								vm.$store.state.user = response.data.username;
								localStorage.setItem("username", response.data.username);
								localStorage.setItem("email", response.data.email);
								vm.$auth.setToken(token);
								// vm.$router.go("/account/overview");
								window.location.href = "/account/overview";
							} else {
								this.$swal.fire({
									icon: "warning",
									title: "Login Failed !",
									text: "Email or passoword fail!"
								});
								vm.error = true;
								vm.submitting = false;
							}
						} else {
							vm.error = true;

							this.$toast.error('Login successfully', {
										timeout: 1500
							});
						}
						vm.submitting = false;
					});
			} else {
				// vm.show_verify = true;
				this.$toast.warning('Click to verify', {
						timeout: 1500
				});
			}
		}
	}
};
</script>

<style scoped>
/* a {
	color: rgb(0, 113, 189);
} */
.login {
	align-items: stretch;
	display: flex;
	justify-content: space-between;

}
.first-wrap {
	padding-right:15px;
}
.typewrite {
	color: #ffc107;
}
</style>



