import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Market from '@/components/Market'
import Trade from '@/components/Trade'
import Login from '@/components/Auth/Login'
import Register from '@/components/Auth/Register2'
import Forgot from '@/components/Auth/Forgot'
import PrivacyPolicy from '@/components/PrivacyPolicy'
import Terms from '@/components/Terms'
import Referral from '@/components/Referral'
import Overview from '@/components/Account/Overview'
import Spotwallet from '@/components/Account/Spotwallet'
import Fundingwallet from '@/components/Account/Fundingwallet'
import Fee from '@/components/Fee'
// import Earn from '@/components/Account/Earn'
import P2Pmarket from '@/components/P2Pmarket'
import Listing from '@/components/Listing'
import MainDeposit from '@/components/Account/wallet/MainDeposit2'
// import MainWithdraw from '@/components/Account/wallet/MainWithdraw'
// import Exchange_Openorder from '@/components/Order/exchange/Openorder'
import My_Referral from '@/components/My/Referral'

Vue.use(Router)

export default new Router({
	mode: 'history',
	routes: [
		{
			path: '/',
			name: 'HelloWorld',
			component: HelloWorld
		},
		{
			path: '/account/overview',
			name: 'Overview',
			component: Overview,
			meta: {
				forAuth: true
			}
		},
		{
			path: '/account/main',
			name: 'Spotwallet',
			component: Spotwallet,
			meta: {
				forAuth: true
			}
		},
		{
			path: '/account/main/deposit',
			name: 'MainDeposit',
			component: MainDeposit,
			meta: {
				forAuth: true
			}
		},
		// {
		//   path: '/account/main/withdraw',
		//   name: 'MainWithdraw',
		//   component: MainWithdraw,
		//   meta: {
		//     forAuth: true
		//   }
		// },
		{
			path: '/account/funding',
			name: 'Fundingwallet',
			component: Fundingwallet,
			meta: {
				forAuth: true
			}
		},
		// {
		//   path: '/account/earn',
		//   name: 'Earn',
		//   component: Earn,
		//   meta: {
		//     forAuth: true
		//   }
		// },
		{
			path: '/my/referral',
			name: 'My_Referral',
			component: My_Referral,
			meta: {
				forAuth: true
			}
		},
		// {
		//   path: '/order/exchange/openorder',
		//   name: 'Exchange_Openorder',
		//   component: Exchange_Openorder,
		//   meta: {
		//     forAuth: true
		//   }
		// },
		{
			path: '/market',
			name: 'Market',
			component: Market
		},
		{
			path: '/p2p-market',
			name: 'P2Pmarket',
			component: P2Pmarket
		},
		{
			path: '/trade/:pair',
			name: 'Trade',
			component: Trade
		},
		{
			path: '/login',
			name: 'Login',
			component: Login,
			meta: {
				forVisitor: true
			}
		},
		{
			path: '/register',
			name: 'Register',
			component: Register,
			meta: {
				forVisitor: true
			}
		},
		{
			path: '/forgot-password',
			name: 'Forgot',
			component: Forgot,
			meta: {
				forVisitor: true
			}
		},
		{
			path: '/privacy-policy',
			name : 'PrivacyPolicy',
			component: PrivacyPolicy,
		},
		{
			path: '/apply-listing',
			name : 'Listing',
			component: Listing
		},
		{
			path: '/fees',
			name : 'Fee',
			component: Fee,
		},
		{
			path: '/terms-and-conditions',
			name : 'Terms',
			component: Terms,
		},
		{
			path: '/referral',
			name : 'Referral',
			component: Referral,
		},
		{ path: '*', redirect: '/' }
	]
})



