<template>
    <div class="myrefferal">
        <Header/>
        <div class="bg-refferal">
        <div class="container">
                <div class="my-refferal">
                    <div class="w-50-percent">
                        <h1 class="refferal-h1 text-white">Refer Friends. Earn Crypto Together.</h1>
                        <div class="mt-30 w-500 text-white">Earn up to 20% commission on every trade across ZiiPex Spot.</div>
                    </div>
                    <div class="w-50-percent">
                        <div style="background-color: #9cbdfc5c;padding: 20px;border-radius: 5px;">
                            <div class="bg-white" style="border-radius: 5px;padding: 10px 15px;">
                                <div class="flex">
                                    <div class="w-50-percent w-400">Referral ID</div>
                                    <div class="w-50-percent w-500 text-right">{{ ref }}</div>
                                </div>
                            </div>
                            <div class="mt-20 bg-white" style="border-radius: 5px;padding: 10px 15px;">
                                <div class="flex">
                                    <div class="w-20-percent w-400">Link</div>
                                    <div class="w-80-percent w-500 text-right" ref="comcopy">https://ziipex.com/register?ref={{ ref }}</div>
                                </div>
                            </div>
                            <div class="mt-10 text-right">
                                <a class="btn btn-primary w-500" @click="copy()">Copy Link Invite Friends</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="box-reffer-body">
            <div class="container">
                <div class="row">
                    <h2>How to refer your referral program ?</h2>
                    <div class="reffer-howto">
                        <div class="reffer-box">
                            <div class="cercle-box">
                                <div class="cercle">1</div>
                            </div>
                            <h3>Step 1</h3>
                            <p>Share your link or QR code to your friends via facebook, line, and others.</p>
                        </div>
                        <div class="reffer-box">
                            <div class="cercle-box">
                                <div class="cercle">2</div>
                            </div>
                            <h3>Step 2</h3>
                            <p>Refer your friend using your referral link or your QR code</p>
                        </div>
                        <div class="reffer-box">
                            <div class="cercle-box">
                                <div class="cercle">3</div>
                            </div>
                            <h3>Step 3</h3>
                            <p>You get commission from ALL your friends trading fees.</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="reffer-boxs">
            <div class="container">
                <div class="row">
                    <h2>My Refferals</h2>
                    <div class="table-min-header">
                        <div class="head mobile-none" style="width: 10%;padding-left: 30px;">No.</div>
                        <div class="head mobile-width-30" style="width: 20%;">UID</div>
                        <div class="head mobile-none" style="width: 20%;">ID</div>
                        <div class="head mobile-width-40" style="width: 25%;text-align: left;">Email</div>
                        <div class="head mobile-width-30" style="width: 20%;text-align: left;">Register</div>
                        <div class="head  mobile-none" style="width: 15%;text-align: right;">Bonus</div>
                        <div class="head mobile-none" style="width: 10%;text-align: right;padding-right: 30px;">Status</div>

                    </div>
                    <div class="table-min-body" v-for="(refer,index) in referrals" :key="index">
                        <div class="body mobile-none" style="width: 10%;text-align: left;padding-left: 30px;">{{  index+1 }}</div>
                        <div class="body mobile-width-30" style="width: 20%;text-align: left;">{{  refer.user.UID }}</div>
                        <div class="body mobile-none" style="width: 20%;text-align: left;">{{  refer.user.id }}</div>
                        <div class="body mobile-width-40" style="width: 25%;text-align: left;">{{  email_sub(refer.user.email) }}</div>
                        <div class="body mobile-width-30" style="width: 20%;text-align: left;">{{  moment_format(refer.user.createdAt) }}</div>
                        <div class="body mobile-none" style="width: 15%;text-align: right;">0.000000</div>
                        <div class="body mobile-none" style="width: 10%;text-align: right;padding-right: 30px;">{{  refer.user.status == ''? '-' : '-' }}</div>
                    </div>
                </div>
            </div>
        </div>
        <Footer/>
    </div>
</template>
<!-- <script src="https://cdnjs.cloudflare.com/ajax/libs/moment.js/2.29.4/moment.min.js"></script> -->
<script>
import axios from 'axios'
import { API_URL } from "../../config.js";
export default {

    name : 'My_Referral',
    data() {
        return {
            ref : '**************',
            link_refferal : '',
            referrals : []
        }
    },
    created () {
        this.getReferral();
    },
    methods : {
        email_sub(data) {
            let str = data;
            let email_str = str.replace(/(\w{4}).*?@/, '$1****@');
            return email_str;
        },
        moment_format(data_date) {
            let data_time = this.$moment(data_date).format("DD-MM-YYYY HH:mm");
            console.log(data_time)
            return data_time;
        },
        async copy () {
            const text_link = 'https://ziipex.com/register?ref='+this.ref;

            try {
                await navigator.clipboard.writeText(text_link);
                // this.$swal.fire('Copied', 'https://ziipex.com/register?ref='+this.ref, 'success')
                this.$toast.success("Copied link", {
                    timeout: 1200
                });
            } catch($e) {
                console.log('Cannot copy');
            }
        },
        getReferral() {
            let vm = this;

            axios.get(API_URL + "/my/referral").then((response) => {
                vm.ref = response.data.ref;
                vm.referrals = response.data.referrals;
            })
        }
    }
}
</script>
<style scoped>
.myrefferal {
    margin: 0;
    padding: 0;
    height: auto;
    width: auto;
    color: #000 !important;
    
}
.bg-refferal {
    padding-top: 50px;
    /* background: linear-gradient(100.72deg,#ff8800d7,#d449f7d7); */
    background-color: #1d2234a2;
    background-image: url("../../assets/img/bg-home.png");
}
.my-refferal {
    display: flex;
    width: 100%;
    padding: 100px 0px;
}
.w-20-percent {
    width: 20%;
}
.w-30-percent {
    width: 30%;
}
.w-40-percent {
    width: 40%;
}
.w-50-percent {
    width: 50%;
}
.w-60-percent {
    width: 60%;
}
.w-70-percent {
    width: 70%;
}
.w-80-percent {
    width: 80%;
}
.refferal-h1 {
    font-weight: 600;
    font-size: 40px;
}

.reffer-boxs {
    border-top: 1px solid #ebebeb;
    padding-bottom: 50px;
    background: #fff;
}
.reffer-boxs h2 {
    font-size: 22px;
    font-weight: 500;
    color: #000;
}
.reffer-boxs .table-min-header {
    color: #2d2d2d;
}
.reffer-boxs .table-min-body {
    color: #2d2d2d;
}
.box-reffer-body {
    padding-bottom: 50px;
    background: #fff;
}
.box-reffer-body h2 {
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    color: #000;
}
.reffer-howto {
    margin-top:50px;
    display: flex;
    justify-content: center;
}
.reffer-howto .reffer-box {
    width: 30%;
    padding: 20px 10px;
    text-align: center;
    display: block;
    border: 1px solid rgba(221, 221, 221, 0.623);
}
.reffer-howto .reffer-box .cercle-box {
    display: flex;
    justify-content: center;
}
.reffer-howto .reffer-box .cercle-box .cercle {
    margin-bottom: 10px;
    background: #2e3bc8;
    width: 35px;
    height: 35px;
    line-height: 34px;
    border-radius: 500rem;
    font-weight: 600;
}
.reffer-howto .reffer-box h3 {
    margin-bottom: 15px;
    font-weight: 500;
    font-size: 15px;
    color: rgb(31, 31, 31);
}
.reffer-howto .reffer-box p {
    font-size: 15px;
    color: #2d2d2d;
}

</style>



