<template>
	<div class="login poppin">
		<div class="base-container__left">
			<div style="top:2.3em;margin-bottom: 2.3em;">
				<div class="base-container__left-content">
					<div class="base-logo">
						<router-link to="/"><img src="../../assets/img/logo.png" width="140" style="margin-bottom: 6rem;"
								alt=""></router-link>
					</div>
					<div class="base-container__left-title">
						<h1>
							<span class="first-wrap">The Most</span><router-link to="/login" class="typewrite" data-period="1500" data-type='[ "Trusted", "Secure", "Reliable", "Assets" ]'>
								<span class="wrap">Secure</span>
							</router-link>
							<br>Cryptocurrency
							<br>Exchange.
						</h1>
						<p class="poppin">Get started with the easiest and most secure platform to buy, sell, trade, and earn cryptocurrencies.</p>
					</div>
				</div>

			</div>
		</div>
		<div class="base-container__right">
			<div class="base-container__right-content">
				<div class="form-components__title">
					<h2 class="poppin">Verify Email</h2>
                    <p class="poppin mt-30">Forgot your password? No problem. Just let us know your email address and we will email you a password reset link that will allow you to choose a new one.</p>
				</div>
				<div class="form-components__body">
					<form @submit.prevent="reset">
						<div class="form-components__input-text">
							<div class="form-components__block">
								<label for="UsernameOrEmail" class="form-components__block-label">Email :</label>
								<div class="form-components__block-item block-input-text">
									<span class="form-components__block-icon">
										<img src="../../assets/img/email-icon.png" class="mt_2" height="20" alt="">
									</span>
									<input autocomplete="new-email" type="email" class="form-components__block-input" v-model="from.email" required autofocus>
								</div>
							</div>
							
							<div class="form-components__block">
								<div id="captcha" style="min-height: 39px; background-color: #181c2b"></div>
								<div v-show="show_verify" class="font-12" style="color: red; display: none">
									* Click to verify
								</div>
								<div class="form-components__block-item block-button">
									<button type="submit" class="form-components__block-button">
										Email Password Reset Link
									</button>
								</div>
							</div>
							<div class="form-components__block flex">
								<router-link :to="'/login'" class="form-components__block-link">Go to login page</router-link>
							</div>
							
						</div>
					</form>
					
				</div>


			</div>

		</div>
	</div>
</template>

<script>
import axios from "axios";
import { API_URL } from "../../config.js";

export default {
	name: "Forgot",
	data() {
		return {
			host: new URL(location.href).host,
			loginAuth: true,
			error: false,
			success: false,
			submitting: false,
			show_verify: false,
			validate: false,
			from: {
				email: "",
				data: []
			}
		};
	},
	metaInfo() {
		return {
			title: "Forgot password | ZiiPEX.com",
			htmlAttrs: {
				lang: "en",
				amp: true
			},
			meta: [
				{ name: "description", content: "Forgot your password? No problem. Just let us know your email address and we will email you a password reset link that will allow you to choose a new one." },
				{ property: "og:title", content: "Forgot password | ZiiPEX.com" },
				{ property: "og:site_name", content: "ZiiPex" },
				{ property: "og:type", content: "website" },
				{ name: "robots", content: "index,follow" }
			]
		};
	},
	created() {
		let vm = this;
		// console.log(this.$store.state.user);

		initGeetest4(
			{
				captchaId: "3bc40107fd4cd66d70ff242ea56da494",
				// product: 'popup',
				btnHeight: "39px",
				btnWidth: "100%",
				language: "en",
                bg_color : 50
			},
			function (captcha) {
				captcha.appendTo("#captcha");

				captcha
					.onReady(function () {
						vm.validate = false;
					})
					.onSuccess(function (res) {
						vm.validate = true;
						vm.show_verify = false;
						vm.from.data = captcha.getValidate();
					})
					.onError(function () {
						vm.validate = false;
					});
			}
		);
	},
	mounted() {
		var TxtType = function(el, toRotate, period) {
			this.toRotate = toRotate;
			this.el = el;
			this.loopNum = 0;
			this.period = parseInt(period, 10) || 1500;
			this.txt = '';
			this.tick();
			this.isDeleting = false;
		};

		TxtType.prototype.tick = function() {
			var i = this.loopNum % this.toRotate.length;
			var fullTxt = this.toRotate[i];

			if (this.isDeleting) {
			this.txt = fullTxt.substring(0, this.txt.length - 1);
			} else {
			this.txt = fullTxt.substring(0, this.txt.length + 1);
			}

			this.el.innerHTML = '<span class="wrap">'+this.txt+'</span>';

			var that = this;
			var delta = 200 - Math.random() * 100;

			if (this.isDeleting) { delta /= 2; }

			if (!this.isDeleting && this.txt === fullTxt) {
			delta = this.period;
			this.isDeleting = true;
			} else if (this.isDeleting && this.txt === '') {
			this.isDeleting = false;
			this.loopNum++;
			delta = 500;
			}

			setTimeout(function() {
			that.tick();
			}, delta);
		};

		window.onload = function() {
			var elements = document.getElementsByClassName('typewrite');
			for (var i=0; i<elements.length; i++) {
				var toRotate = elements[i].getAttribute('data-type');
				var period = elements[i].getAttribute('data-period');
				if (toRotate) {
				new TxtType(elements[i], JSON.parse(toRotate), period);
				}
			}
			// INJECT CSS
			var css = document.createElement("style");
			css.type = "text/css";
			css.innerHTML = ".typewrite > .wrap { border-right: 0.08em solid #fff;}";
			document.body.appendChild(css);
		};
	},
	methods: {
		reset() {
			let vm = this;
			if (vm.validate == true && vm.from.email != '') {
				vm.axios.post(API_URL + "/reset-password", this.from).then(res => {
					console.log(res.data)
					if(res.data.status) {
						vm.$swal.fire(
								'Success!',
								res.data.message,
								'success'
						)
					}
				})

			}else{
				if(vm.validate == false) {
					this.$toast.warning('Click to verify', {
						timeout: 1500
					});
				}
			}
		}
		
	}
};
</script>

<style scoped>
/* a {
	color: rgb(0, 113, 189);
} */
.login {
	align-items: stretch;
	display: flex;
	justify-content: space-between;

}
.first-wrap {
	padding-right:15px;
}
.typewrite {
	color: #ffc107;
}
</style>



