            }
        },
        async sell(type) {
            let vm = this;
            if(type == 'limit')
            {
                console.log('sell limit')
                if(vm.from.sell_amount > 0)
                {
                    axios.post(API_URL+'/sell-limit',vm.from).then((res) => {
                        console.log(res.data)
                        vm.openOrders(vm.from.ticker_id);

                        if(res.data.status) {
                            this.$toast.success("Sell limit success", {
                                timeout: 1200
                            });
                        }else{
                            this.$toast.warning(res.data.bill, {
                                timeout: 1200
                            });
                        }
                    }) 
                }else{
                    // console.log('sell limit balance avilable')
                    this.error_sell_limit = true;
                }
            }else{
                console.log('sell market')
                if(vm.from.sell_amount > 0)
                {
                    axios.post(API_URL+'/sell-market',vm.from).then((res) => {
                        console.log(res.data)
                        // vm.openOrders(vm.from.ticker_id);
                        if(res.data.status) {
                            this.$toast.success("Sell market success", {
                                timeout: 1200
                            });
                        }else{
                            this.$toast.warning(res.data.bill, {
                                timeout: 1200
                            });
                        }
                    }) 
                }else{
                    this.error_sell_market = true;
                }
            }
        },
        cancel(uid) {
            let vm = this;
            axios.post(API_URL+'/cancel-order/'+this.ticker_id,{id : uid}).then((res) => {
                console.log(res.data)
                vm.openOrders(vm.from.ticker_id);
                this.$toast.success("Cancel order", {
                    timeout: 1200
                });
            })
        },
        bid_add (value) {
            this.from.sell_price = parseFloat(value);
        },
        ask_add (value) {
            this.from.buy_price = parseFloat(value);
        },
    }
}
</script>
<style scoped>
.bg-percent-trade {
    background-size: 50% 100%;background-image: linear-gradient(#8a92a7, #8a92a7);
    background: #11141d;
    /* border-radius: 5px; */
    background-repeat: no-repeat;
}

.cancel-order {
    color: #f05d13;
}

</style>


